import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import {HomeAplicationPageModule} from '../pages/home-aplication/home-aplication.module';
import {AvisoPrivacidadPageModule} from  '../pages/aviso-privacidad/aviso-privacidad.module';
import {NuevoUsuarioPageModule} from '../pages/nuevo-usuario/nuevo-usuario.module';

import { Facebook } from '@ionic-native/facebook';


import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';

@NgModule({
  declarations: [
    MyApp,
    HomePage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp),
    HomeAplicationPageModule,
    NuevoUsuarioPageModule,
    AvisoPrivacidadPageModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Facebook,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



